import React from 'react'
import { NavLink } from 'react-router-dom'

const CardChampions = ({props}) => {

  return (
    <React.Fragment>
      <NavLink to={{pathname: `/championstats/${props?.id}`}} className="name-link">
        <div className="champcard">
          <div className="MainContent">
            <div className="cardcontent">
              <div className="cardImg" role="img">
                <span>
                  <img src={props?.image} alt={props?.id} className="champimg"/>
                </span>
              </div>
              <div className="champname">{props?.name}</div>
              <div className="champnick" style={{textTransform: "capitalize"}}>{props?.title}</div>
              {/* <div className="champsRole">
                <img src={Top} alt="Top" />
              </div> */}
            </div>
          </div>
        </div>
      </NavLink>

    </React.Fragment>
  )
}

export default CardChampions
